.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

* {
    font-family: 'Roboto', sans-serif;
}

.App {
    background: url('imgs/SplitShire_IMG_9970.jpg');

    background-size: 100% 100%;
    height: 39.8em;
    width: 100%;

}

.seeThruHeader {
    background: -webkit-gradient(linear, right top, left top, from(red), to(purple));
    background: -webkit-linear-gradient(right, red, purple);
    background: -o-linear-gradient(right, red, purple);
    background: linear-gradient(to left, red, purple);
    position: absolute;
    top: 0;
    opacity: 0.3;
    height: 39.8em;
    width: 100%;
}

#pageIntro {
    display: inline-block;

    padding-bottom: 5px;
    border-bottom: 0.01em solid white;
    font-size: 4vw;

}

#subHeader {
    font-size: 3vw;

}

h1, h2 {
    position: relative;
    text-shadow: 1px 1px 1px #000;
    color: white;
    margin: 0;

}

ul {
    list-style: none;
    padding: 0;

}

p {

    color: white;
    font-size: 1.5em;
    border-color: red;
}


li {
    border-color: white;

}

#list {
    border-bottom-left-radius: 0.3em;
    border-bottom-right-radius: 0.3em;
    z-index: 1;
    right: 2.05em;
    background: black;
    display: inline-block;
    width: 9.5em;
    top:39.8em;
    position: absolute;
}


.title{
    font-size:2em;
    position: absolute;
    top:0em;


}
.aboutMe{
    overflow: hidden;
    background-color: white;
    z-index: -2;
    position: absolute;
    top:39.8em;
    width: 100%;
    height: 100%;
}

#startOfList{
    overflow: hidden;
    bottom: 0;
    right:3em;
    position: absolute;
    width: 11.9%;
    height: 11.9%;
    background-color: black;
    z-index: 3;
    padding: 0;
    border: none;

}
a {
    text-decoration: none;

}
#turnUpsideDown{
    -webkit-animation: MoveUpDown 1s linear infinite;
            animation: MoveUpDown 1s linear infinite;;
    display: inline-block;
    position: absolute;

    left: 35%;
    font-size: 65px;
    padding-top: 10px;
    padding: 0;
    margin: 0;
}
@-webkit-keyframes MoveUpDown {
    0% {
        bottom: 0;
    }
    50% {
        bottom: 10px;
    }
    100% {
        bottom: 0;
    }
}
@keyframes MoveUpDown {
    0% {
        bottom: 0;
    }
    50% {
        bottom: 10px;
    }
    100% {
        bottom: 0;
    }
}

.imgOfCreator{
    left:36%;
    position: absolute;
    top:12em;
    height: 22em;
    width: 22em;
    border-radius: 12em;

}
.aboutMe>*,div>.title{
    color: #FF5671;
}
#detailsStart{
    color:#FF5671 ;
    left:0;

    position: absolute;
}
.aboutMe>.subtextHeader,div>.title {

    border-bottom: #FF5671;
    border-bottom: 0.01em solid #FF5671;
}
.aboutMeHeader{

    font-weight:100;
    margin: 0;
    top:0;
    font-size: 6em;
    position: absolute;
    left: 40%;
}
.subtextHeader{
    border-bottom: 0.001em solid;
    position: absolute;
    top:4em;
    left:40%;

}
#aboutMeText{
    text-align: left;
    color: black;
    font-size: 1em;
    position: absolute;
    width: 20em;
    height: 10em;
    top:4em;

}
#colorBox{

    top: 12em;
    left:8em;
    height: 25em;
    width: 25em;
    position: absolute;
}
#details{

    position: absolute;
    right: 11em;
    top:12em;
    width: 15em;
    height: 25em;
    overflow: hidden;
}
.expSec{
    background-color: #FF5671;
    position: absolute;
    top:80em;
    height: 40em;
    width: 100%;
}
#detailsStart,#detailsContent{
    color: #FF5671;
}
#detailsContent,#name{
    top:1.50em;
    position: absolute;
}
#name {
    color: black;
    font-size: 1.25em;
    margin-top: 25px;
}



